import React from "react"
import { Container, Grid, Image, Header, Button } from "semantic-ui-react"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import querystring from "query-string"
import { labels } from "../../selections"
import Layout from "../layout"
import "./override.css"

const Selection = ({ selection, description, options, previewSrc, result }) => (
  <Layout>
    <Container fluid>
      <Grid padded stackable>
        <Grid.Column width={5}>
          <Header>Your selection: {labels[selection]}</Header>
          <p>{description}</p>
          {options}
        </Grid.Column>
        <Grid.Column width={11}>
          <Grid>
            <Grid.Row columns="equal">
              <Grid.Column>
                <AniLink swipe direction="right" to="/steps/1">
                  <Button fluid>Back to Selection</Button>
                </AniLink>
              </Grid.Column>
              <Grid.Column>
                <AniLink
                  swipe
                  direction="left"
                  to={`/steps/3?${querystring.stringify(result)}`}
                >
                  <Button fluid>
                    Submit Design for Pricing and Consultation
                  </Button>
                </AniLink>
              </Grid.Column>
            </Grid.Row>
            <Grid.Row>
              <Grid.Column>
                <Image src={previewSrc} />
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Grid.Column>
      </Grid>
    </Container>
  </Layout>
)

export default Selection
